* {
    box-sizing: border-box;
    font-family: 'Josefin Sans', sans-serif;
    list-style: none;
    margin: 0;
    padding: 0;
    text-decoration: none;
}

a:hover {
    text-decoration: none !important;
}

.oculto {
    margin-left: 23px;
}

body {
    background: #f3f5f9;
}

.wrapper {
    display: flex;
    position: relative;
}


.wrapper .sidebar {
    position: fixed;
    z-index: 1;
    width: 0px;
    top: 0;
    left: 0;
    overflow-x: hidden;
    height: 100%;
    background-color: #4b4276;
    padding: 30px 0;
    transition: 0.5s;
    overflow: hidden;
}

@media (max-width: 1200px){
    .wrapper .sidebar{
        width: 0px;
    }
}
.wrapper .sidebar h2 {
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 30px;
}

.wrapper .sidebar ul li {
    padding: 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    border-top: 1px solid rgba(225, 225, 225, 0.05);
}

.wrapper .sidebar ul li a {
    color: #dbb8d7;
    display: block;
}

.wrapper .sidebar ul li a .fas {
    width: 25px;
}

.wrapper .sidebar ul li:hover {
    background: #594f8d;
}

.wrapper .sidebar ul li:hover a {
    color: #fff;
}

.wrapper .sidebar .footer_sidebar {
    bottom: -10px;
    color: #dbb8d7;
    display: flex;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    text-align: center;
}

.wrapper .main_content {
    transition: margin-left .5s;
    width: 100%;
    margin-left: 0px;

}

.wrapper .main_content h1 {
    padding-bottom: 0px;
}

#show_sidebar{
    font-size: 2em;
    margin-left: 10px;
}

#show_sidebar:hover{
    cursor: pointer;
}

.page_inicial{
    margin-bottom: 15px;
}

#close_sidebar{
    font-size: 2em;
    padding-right: 5px;
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 36px;
    margin-left: 50px;
}

#close_sidebar:hover{
    cursor: pointer;
}


#sistema span{
    text-shadow: 0px 10px 15px rgb(102, 102, 102) ;
}
.titulo {
    transform: scale(0.2) translateY(50vh);
    opacity: 0;
    -webkit-animation: 0.5s ease 0s animacaoTitulo forwards;
            animation: 0.5s ease 0s animacaoTitulo forwards;
  }
  
  @-webkit-keyframes animacaoTitulo {
    from {
      transform: scale(0.2) translateY(0vh);
      opacity: 0;
    }
    to {
      transform: scale(1) translateY(0);
      opacity: 1;
    }
  }
  
  @keyframes animacaoTitulo {
    from {
      transform: scale(0.2) translateY(0vh);
      opacity: 0;
    }
    to {
      transform: scale(1) translateY(0);
      opacity: 1;
    }
  }

